<!-- 我的收徒：奖励-->
<template>
  <div class="apprentice-income">
    <div class="top-info text-center">
      <span class="cumulative-amount">累计</span> <span class="amount" v-text="processAmount(totalIncome)"></span> <img class="yuan" src="//qianka.b0.upaiyun.com/images/fe75cc4d419a21a245c2d3e3fffca345.png" alt="">
    </div>
    <div class="bottom-info">
      <div class="title">
        近30天收徒奖励
      </div>

      <div class="history">
        <div class="history-row" v-for="(item, index) in daylyIncome" :key="index" :class="{today: index===0}">
          <div class="row-inner">
            <span class="time" v-text="formatDate(item.day)"></span>
            <span class="amount" v-text="parseFloat(item.income)"></span>
            <span class="yuan">¥</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style lang="scss">
  @import '../../../scss/mixins';

  .apprentice-income {
    /*max-height: 463px;*/
    height: 100%;
    overflow-x: hidden;
    overflow-y: scroll;

    .top-info {
      margin-top: 20px;

      span {
        display: inline-block;
        color:  #3A3A3A;
      }

      .cumulative-amount {
        font-family: PingFang-SC-Medium;
        font-size: 13px;
        line-height: 18px;
      }

      .amount {
        font-family: giorgio;
        font-size: 60px;
        line-height: 60px;
        height: 60px;
        position: relative;
        top: 3px;
      }

      .yuan {
        width: 16px;
        height: 16px;
        position: relative;
        top: 3px;
      }
    }

    .bottom-info {
      .title {
        margin-top: 24px;
        margin-left: 20px;

        @include font-regular();

        font-size: 14px;
        color: #DDDDDD;
      }

      .history {
        margin-top: 20px;
        padding: 0 40px 0 36px;

        .history-row {
          height: 40px;
          border-left: 1px solid #E4E4E4;
          padding-left: 23px;
          color: #B5B5B5;
          font-size: 14px;
          line-height: 20px;
          position: relative;

          .row-inner {
            overflow: hidden;
            position: absolute;
            top: -6px;
            width: 100%;
          }

          &:before {
            content: '';
            width: 9px;
            height: 9px;
            border-radius: 5px;
            position: absolute;
            left: -5px;
            border: 1px solid #fff;
            background-color: #E4E4E4;
          }

          @include font-regular();

          .amount, .yuan {
            float: right;
            @include font-medium();
          }

          .yuan {
            font-size: 11px;
            margin-top: 3px;
            margin-right: 2px;
            line-height: 16px;
            vertical-align: bottom;
          }

          &.today {
            color: #44B549;

            &:before {
              background-color: #44B549;
            }
          }
        }

        .history-row:last-child {
          border-left: none;
          &:before {
            left: -4px;
          }
        }
      }
    }
  }
</style>

<script>
  export default {
    props: {
      totalIncome: {
        type: Number,
        default: 0
      },
      noIncomeData: Boolean,
      daylyIncome: Array
    },

    data () {
      return {
        maxAmount: 0,
        today: null
      }
    },

    methods: {
      formatDate (date) {
        return date.substr(0, 10).replace(/-/g, '.')
      },

      processAmount (amount) {
        let m = parseFloat(amount)
        return Number.isNaN(m) ? 0 : m
      }
    }
  }
</script>



